import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '@/view/FrontEnd/HomeView.vue'

Vue.use(VueRouter)



const routes = [
    
  { 
    path: '/', 
    name: 'home',
    component: home
  },

]

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes,
})


export default router
