
<template>
  <section id="text-item">
    <swiper :options="swiperOption" ref="mySwiper" @reachBeginning="swiperBeginning" @reachEnd="swiperEnd">
      <swiper-slide v-for="(slide, index) in Items" :key="index">
        {{ slide }}
        <img src="@/assets/man.jpg" alt="test" />
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <div class="text-bg">

    </div>
  </section>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'


export default {
  name: 'TextItem',
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      swiperOption: {
        mousewheel: {
          invet: true,
        },
        pagination: {
          el: '.swiper-pagination'
        },

      },
      Items: ['안녕하세요', '반갑습니다.', '최고의 품질', '저희만 있습니다.'],
      ui: {
        activeIndex: 1,
      }
    }
  },
  methods: {
    swiperEnd() {
      const swiper = this.$refs.mySwiper.swiper
      setTimeout(function () {
        swiper.params.touchReleaseOnEdges = true;
        swiper.params.mousewheel.releaseOnEdges = true;
      }, 500);
    },
    swiperBeginning() {
      const swiper = this.$refs.mySwiper.swiper
      setTimeout(function () {
        swiper.params.touchReleaseOnEdges = true;
        swiper.params.mousewheel.releaseOnEdges = true;
      }, 500);
    }
  },
  computed: {
    swiper() {
      // console.log(this.$refs.mySwiper.swiper)
      return this.$refs.mySwiper.swiper
    }
  },
  mounted() {
    console.log(this.swiperOption)
    // current swiper instance
    // console.log('this is current swiper instance object', this.swiper)
    // this.swiper.slideTo(3, 1000, false)
  }
}
</script>
