<template>
	<div>
		<text-item />
		<div class="body">
			<section class="parallax" data-speed="0.7">
				<h1>This is parallax</h1>
			</section>

			<section class="parallax" data-speed="0.7">
				<h1>This is parallax</h1>
			</section>

			<section class="parallax" data-speed="0.7">
				<h1>This is parallax</h1>
			</section>
		</div>
	</div>
</template>

<script>
import TextItem from './TextItem.vue';

export default {
	name: 'homeView',
	components: { TextItem },
	data() {
		return {
			parallax: null,
		};
	},
	mounted() {
		const parallax = Array.from(document.querySelectorAll('.parallax'));
		window.onscroll = () => {
			parallax.forEach((el) => {
				// apparent scroll speed of the background
				// defaults to half the scroll amount
				const speed = el.dataset.speed || 0.5;
				const windowYOffset = window.pageYOffset;
				const newBgPos = '50% ' + windowYOffset * speed + 'px';

				el.style.backgroundPosition = newBgPos;
			});
		};
	},
	methods: {
		test() {},
	},
};
</script>
